.picture-grid {
	display: block;
	width: 100%;
	height: auto;
	margin-left: auto;
	margin-right: auto;
}
.col:hover .picture-grid-description {
	visibility: visible;
	cursor: pointer;
}
.picture-grid-description {
	visibility: hidden;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	color: white;
}
.picture-grid-detail {
	width: 100%; 
	height: auto;
}
.col:hover .picture-grid-detail{
	cursor: pointer;
	-webkit-filter: brightness(60%);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.row {
	display: block;
	width: 100%;
	height: 45%;
}
.col {
	display: inline-block;
	position: relative;
	width: 23%;
	height: 100%;
	margin: 3px;
}
.title {
	color: #3399ff;
